@OptIn(ExperimentalComposeUiApi::class)
@Composable
fun CustomTopBar(
    modifier: Modifier = Modifier
) {
    val configuration = LocalWindowInfo.current
    Card(
        modifier = modifier, backgroundColor = CardNavigationColor.copy(0.2F),
        shape = RoundedCornerShape(24.dp), elevation = 0.dp
    ) {
        Box(Modifier.fillMaxSize().padding(horizontal = 20.dp)) {
            var currentIndex by remember {
                mutableStateOf(0)
            }
            val listTopBarItem = remember {
                mutableStateListOf<Pair<Int, Offset>>()
            }
            Row(
                modifier = Modifier.fillMaxSize(),
                verticalAlignment = Alignment.CenterVertically, horizontalArrangement = Arrangement.Center
            ) {
                Text(
                    "Alif Meidy Ferdiansyah",
                    fontWeight = FontWeight.SemiBold,
                    fontSize = when {
                        configuration.containerSize.width >= 898 -> 26.sp
                        configuration.containerSize.width >= 647 -> 24.sp
                        configuration.containerSize.width < 638 -> 17.sp
                        else -> 0.sp
                    },
                    color = Color.White
                )
                if (configuration.containerSize.width > 695) {
                    Spacer(modifier = Modifier.weight(1F))
                    topBarMenu.forEachIndexed { index, name ->
                        Text(
                            name,
                            color = Color.White,
                            modifier = Modifier.clickable(
                                interactionSource = MutableInteractionSource(),
                                indication = null,
                                onClick = {
                                    currentIndex = index
                                })
                                .pointerHoverIcon(PointerIcon.Hand)
                                .onGloballyPositioned {
                                    listTopBarItem.add(
                                        Pair(
                                            index,
                                            it.boundsInParent().bottomCenter)
                                    )
                                }
                        )
                        Spacer(modifier = Modifier.width(24.dp))
                    }

                }
            }
            Card(modifier = Modifier.width(20.dp).height(5.dp).offset {
               return@offset listTopBarItem.find {
                    it.first == currentIndex
                }?.second?.let {
                    IntOffset(it.round().x - 10, it.round().y)
                }?: IntOffset(0,0)
            }, backgroundColor = Color.Green) {}
        }
    }
}

@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
private fun HomeView(
    modifier: Modifier = Modifier
) {
    val configuration = LocalWindowInfo.current
    val density = LocalDensity.current
    Box(modifier) {
        Image(
            painter = painterResource(Res.drawable.pattern),
            contentDescription = "",
            contentScale = ContentScale.Crop
        )
        Row(modifier = Modifier.fillMaxSize()) {
            Column {
                Spacer(modifier = Modifier.height(with(density) {
                    (configuration.containerSize.height * 0.25F).toDp()
                }))
                Text(
                    "Alif Meidy Ferdiansyah",
                    color = Color.White,
                    style = TextStyle(
                        fontSize = when {
                            configuration.containerSize.width > 900 -> 78.sp
                            configuration.containerSize.width >= 751 -> 60.sp
                            configuration.containerSize.width <= 750 -> 50.sp
                            else -> 0.sp
                        }, fontWeight = FontWeight.Bold
                    )
                )
                Spacer(modifier = Modifier.height(16.dp))
                Text(
                    "Android developer passionate in native\nmobile & multiplatform technology environment",
                    color = DescriptionColor,
                    style = TextStyle(
                        fontSize = when {
                            configuration.containerSize.width > 900 -> 32.sp
                            configuration.containerSize.width >= 751 -> 26.sp
                            configuration.containerSize.width <= 750 -> 20.sp
                            else -> 0.sp
                        }
                    )
                )
            }
            Spacer(modifier = Modifier.weight(0.4F))
            Box(modifier = Modifier.padding(top = with(density) {
                (configuration.containerSize.height * 0.2F).toDp()
            })) {
                val interactionSource = remember {
                    MutableInteractionSource()
                }
                val secondCardInteractionSource = remember {
                    MutableInteractionSource()
                }
                var shouldAnimateFirstCard by remember {
                    mutableStateOf(false)
                }
                var shouldAnimateSecondCard by remember {
                    mutableStateOf(false)
                }
                LaunchedEffect(Unit) {
                    delay(500)
                    shouldAnimateFirstCard = true
                    delay(100)
                    shouldAnimateSecondCard = true
                }
                val isFirstCardHovered by interactionSource.collectIsHoveredAsState()
                val isSecondCardHovered by secondCardInteractionSource.collectIsHoveredAsState()
                val animateSecondCard = animateFloatAsState(
                    targetValue = if (isFirstCardHovered) 0.2F else 1F, animationSpec = tween(400)
                )
                val animateFirstCard = animateFloatAsState(
                    targetValue = if (isFirstCardHovered) 1F else if (isSecondCardHovered) 0.5F else 0.9F,
                    animationSpec = tween(400)
                )
                androidx.compose.animation.AnimatedVisibility(
                    shouldAnimateFirstCard, enter = slideInVertically(tween(200)) + fadeIn(
                        tween(400)
                    )
                ) {
                    Card(
                        modifier = Modifier.size(300.dp)
                            .hoverable(interactionSource).graphicsLayer {
                                this.alpha = animateFirstCard.value
                            }.clickable {
                                linkOpener("https://kotlinlang.org/")
                            },
                        backgroundColor = CardNavigationColor.copy(0.4F), elevation = 0.dp
                    ) {
                        Image(painter = painterResource(Res.drawable.kotlin_icon), contentDescription = "kotlin")
                    }
                }
                androidx.compose.animation.AnimatedVisibility(
                    shouldAnimateSecondCard, enter = slideInVertically(tween(200)) + fadeIn(
                        tween(400)
                    ), modifier = Modifier.offset {
                        IntOffset(100, 100)
                    }
                ) {
                    Card(
                        modifier = Modifier.size(300.dp).hoverable(secondCardInteractionSource).graphicsLayer {
                            this.alpha = animateSecondCard.value
                        }.clickable {
                            linkOpener("https://developer.android.com/jetpack/compose")
                        },
                        backgroundColor = CardNavigationColor.copy(0.4F), elevation = 0.dp
                    ) {
                        Image(painter = painterResource(Res.drawable.compose_icon), contentDescription = "compose")
                    }
                }

            }
            Spacer(modifier = Modifier.weight(0.2F))
        }
    }
}
